
// @mui
import { Container } from '@mui/material';
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import HabukhanCreditUser from '../../sections/admin/user/list/CreditUser';

export default function CreditUser(){
    const { themeStretch } = useSettings();
   
   
  
    return (
      <Page title="Admin || Credit/ Debit User Account">
        <Container maxWidth={themeStretch ? false : 'lg'}>
          <HeaderBreadcrumbs
            heading={'Credit / Debit  User Wallet'}
            links={[
              { name: 'Dashboard', href: PATH_ADMIN.root },
              { name: 'user-list', href: PATH_ADMIN.user.all_user },
            ]}
          />
            
          <HabukhanCreditUser />
        </Container>
      </Page>
    );
} 
