import * as Yup from 'yup';
import { useState, } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { capitalCase } from 'change-case';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack, Typography } from '@mui/material';
import axios from '../../../../utils/axios';
import { FormProvider, RHFSelect, RHFTextField, RHFSwitch } from '../../../../components/hook-form';

import Scrollbar from '../../../../components/Scrollbar';
import UserSearch from '../../UserSearch';
import HabukhanUser from '../../HabukhanUser';

// drop dwon style
import useIsMountedRef from '../../../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------

export default function HabukhanCreditUser() {
  const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
    wallet: Yup.string().required('Kindly Select Account Wallet'),
    amount: Yup.string().required(),
    credit: Yup.string().required('Credit/Debit Type Required'),
    reason: Yup.string().required()
  });

  const [userUsername, SetUserUsername] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [isSearchFocused, setSearchFocused] = useState(false);
  const displayResults = userUsername && isSearchFocused;
  const AccessToken = window.localStorage.getItem('accessToken');

  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  const debit = [
    { label: 'Credit User', value: 'credit' },
    { label: 'Debit User', value: 'debit' }
  ];
  const wallet = [
    { label: 'User Wallet', value: 'wallet' },
    { label: 'MTN CG Wallet', value: 'mtn_cg_bal' },
    { label: 'MTN SME Wallet', value: 'mtn_sme_bal' },
    { label: 'MTN GIFTING Wallet', value: 'mtn_g_bal' },
    { label: 'AIRTEL CG Wallet', value: 'airtel_cg_bal' },
    { label: 'AIRTEL SME Wallet', value: 'airtel_sme_bal' },
    { label: 'AIRTEL GIFTING Wallet', value: 'airtel_g_bal' },
    { label: 'GLO CG Wallet', value: 'glo_cg_bal' },
    { label: 'GLO SME Wallet', value: 'glo_sme_bal' },
    { label: 'GLO GIFTING Wallet', value: 'glo_g_bal' },
    { label: '9MOBILE CG Wallet', value: 'mobile_cg_bal' },
    { label: '9MOBILE SME Wallet', value: 'mobile_sme_bal' },
    { label: '9MOBILE GIFTING Wallet', value: 'mobile_g_bal' },
  ];
  const {
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;
  const onSubmit = async (data) => {
    try {
      const resp = await axios.post(`/api/credit/user/only/admin/secure/${AccessToken}/verified/by/system`, {
        user_username: userUsername,
        wallet: data.wallet,
        amount: data.amount,
        credit: data.credit,
        isnotif: data.isNotif,
        reason: data.reason
      })
      swal('success', `You have Successfully ${capitalCase(data.credit)} ${capitalCase(userUsername)} ₦${data.amount} @ '${resp.data?.account_type}'`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };
  const handleSearchFocus = () => {
    setSearchFocused(true);
  };
  const handleChangeSearch = async (event) => {
    const { value } = event.target;
    SetUserUsername(value);
    if (value) {
      try {
        const response = await axios.post(`/api/filter/user/details/admin/by/habukhan/${AccessToken}/secure/react`, {
          username: value
        });
        setSearchResults(response.data.user);
      } catch (error) {
        console.log(error)
      }
    } else {
      setSearchResults([]);
    }

  };
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
              <UserSearch
                query={userUsername}
                onChange={handleChangeSearch}
                onFocus={handleSearchFocus}
              />
              {displayResults &&
                <Scrollbar>
                  <HabukhanUser query={userUsername} results={searchResults} onSelectContact={(e) => {
                    SetUserUsername(e.username); setSearchFocused(false)
                  }} />
                </Scrollbar>}
              <RHFSelect name="credit" label="Debit/Credit" placeholder="Debit/Credit">
                <option value="" />
                {debit?.map((option) => (
                  <option key={option?.label} value={option?.value}>
                    {option?.label}
                  </option>
                ))}
              </RHFSelect>


              <RHFSelect name="wallet" label="Wallet Funding" placeholder="Wallet Funding">
                <option value="" />
                {wallet?.map((option) => (
                  <option key={option?.label} value={option?.value}>
                    {option?.label}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="amount" label="Amount" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="reason" label="Reason" multiline="true" rows="3" />
              <RHFSwitch
                name="isNotif"
                labelPlacement="start"
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      Email Notification
                    </Typography>
                  </>
                }

                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
